.tool {
    font-size: 1.5rem;
    padding: 0.25rem;
    opacity: 0.6;
    transition-property: opacity, transform;
    transition-duration: .25s; 
}

.tool:hover {
    cursor: pointer;
    opacity: 1;
    transform: scale(1.25);
}

.toolicon:focus {
    outline: 0;
}

.tooltip {
    position: relative;
    display: inline-block;
}

.tooltip .tooltip-text {
    font-size: 0.6rem;
    visibility: hidden;
    display: inline-block;
    white-space: nowrap;
    background-color: transparent;
    text-align: center;
    position: absolute;
    z-index: 1;
    top: 100%;
    left: 50%;
    transform: translate(-50%, -85%);
}
  
.tooltip:hover .tooltip-text {
    visibility: visible;
}